/**
 * Pekld anglick error messages do etiny
 * @param {string} errorMessage - Pvodn error message
 * @returns {string} - Peloen error message
 */
export function translateErrorMessage(errorMessage) {
  if (!errorMessage || typeof errorMessage !== 'string') {
    return errorMessage || 'Neznm chyba';
  }

  // Pekldme timeout chyby
  if (errorMessage.includes('timeout') && errorMessage.includes('exceeded')) {
    const match = errorMessage.match(/timeout of (\d+)ms exceeded/);
    if (match) {
      const timeoutMs = match[1];
      const timeoutSec = Math.round(timeoutMs / 1000);
      return `Pekroen asov limit ${timeoutSec}s - server neodpovdl vas`;
    } else {
      return 'Pekroen asov limit - server neodpovdl vas';
    }
  }

  // Pekldme dal bn axios/network chyby
  if (errorMessage.includes('Network Error')) {
    return 'Chyba st - zkontrolujte internetov pipojen';
  }

  if (errorMessage.includes('Request failed with status code 500')) {
    return 'Chyba serveru (kd 500) - zkuste to znovu pozdji';
  }

  if (errorMessage.includes('Request failed with status code 404')) {
    return 'Poadovan zdroj nebyl nalezen (kd 404)';
  }

  if (errorMessage.includes('Request failed with status code 403')) {
    return 'Nemte oprvnn k tto operaci (kd 403)';
  }

  if (errorMessage.includes('Request failed with status code 401')) {
    return 'Neautorizovan pstup - zkuste se znovu pihlsit (kd 401)';
  }

  if (errorMessage.includes('ECONNREFUSED') || errorMessage.includes('connection refused')) {
    return 'Nelze se pipojit k serveru - server je pravdpodobn nedostupn';
  }

  if (errorMessage.includes('ENOTFOUND') || errorMessage.includes('getaddrinfo ENOTFOUND')) {
    return 'Server nenalezen - zkontrolujte adresu serveru';
  }

  // Pokud nen rozpoznan chyba, vra pvodn message
  return errorMessage;
}

/**
 * Zkrcen verze pro timeout chyby bez dodatenho textu
 * @param {string} errorMessage - Pvodn error message
 * @returns {string} - Peloen error message (krat verze)
 */
export function translateErrorMessageShort(errorMessage) {
  if (!errorMessage || typeof errorMessage !== 'string') {
    return errorMessage || 'Neznm chyba';
  }

  // Pekldme timeout chyby
  if (errorMessage.includes('timeout') && errorMessage.includes('exceeded')) {
    const match = errorMessage.match(/timeout of (\d+)ms exceeded/);
    if (match) {
      const timeoutMs = match[1];
      const timeoutSec = Math.round(timeoutMs / 1000);
      return `Pekroen asov limit ${timeoutSec}s`;
    } else {
      return 'Pekroen asov limit';
    }
  }

  // Pro ostatn chyby pouij plnou funkci
  return translateErrorMessage(errorMessage);
}